@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("Oswald"), url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
}
@font-face {
  font-family: "Oswald Medium";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: local("Oswald Medium"), url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
}
html {
  height: 100%;
}

body {
  box-sizing: border-box;
  min-width: 320px;
  min-height: 100%;
  font-weight: 400;
  font-size: 16px;
  line-height: 21px;
  font-family: "Oswald", "Arial", sans-serif;
  color: #000000;
  background-color: #ffffff;
}

img {
  height: auto;
  object-fit: contain;
}

a {
  text-decoration: none;
  color: inherit;
}

li {
  list-style: none;
}

ul {
  padding: 0;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.promo {
  width: 100%;
  min-height: 480px;
}
@media (min-width: 500px) {
  .promo {
    min-height: 600px;
  }
}
@media (min-width: 600px) {
  .promo {
    min-height: 700px;
  }
}
@media (min-width: 768px) {
  .promo {
    min-height: 950px;
  }
}
@media (min-width: 1200px) {
  .promo {
    min-height: 696px;
    background-image: url("../img/index-background-desktop@1x.jpg");
    background-repeat: no-repeat;
    background-size: 50% 100%;
    background-position: 100% 0%;
  }
}

.container {
  min-width: 320px;
  height: 100%;
  padding: 0 20px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .container {
    width: 768px;
    padding: 0 30px;
  }
}
@media (min-width: 1200px) {
  .container {
    width: 1200px;
    padding: 0 60px;
  }
}

.btn {
  width: 280px;
  height: 40px;
  border: none;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  text-align: center;
  text-transform: uppercase;
  color: #FFFFFF;
  background-color: #68B738;
  cursor: pointer;
  transition: 0.25s;
}
.btn:hover {
  background-color: #5EAA2F;
}
.btn:active {
  color: rgba(255, 255, 255, 0.6);
}

.btn-openMore {
  background-color: #F2F2F2;
  color: #444444;
}
.btn-openMore:hover {
  background-color: #F2F2F2;
}
.btn-openMore:active {
  color: rgba(0, 0, 0, 0.3);
}
@media (min-width: 1200px) {
  .btn-openMore {
    width: 168px;
  }
}

.header {
  width: 100%;
  height: 66px;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .header {
    height: 109px;
  }
}
@media (min-width: 1200px) {
  .header {
    background-color: transparent;
  }
}

.header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 11px;
}
@media (min-width: 768px) {
  .header__inner {
    padding-top: 24px;
  }
}

.header__logo-group {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 191px;
  cursor: pointer;
}
@media (min-width: 768px) {
  .header__logo-group {
    width: 174px;
  }
}

.header__logo-img {
  width: 100%;
  max-width: 174px;
}
@media (min-width: 768px) {
  .header__logo-img {
    max-width: 200px;
  }
}

@media (min-width: 768px) {
  .header__logo-text {
