  }
}

@media (max-width: 767px) {
  .navbar-wrapper {
    position: absolute;
    top: 0;
    right: 0;
    width: 80%;
    height: 100vh;
    padding: 50px 0 50px;
    background-color: #68B738;
    z-index: 100;
    opacity: 0;
    visibility: hidden;
    transition: 0.5s all;
  }
}
.navbar__close {
  opacity: 0;
  visibility: hidden;
  position: absolute;
  top: 25px;
  right: 25px;
  cursor: pointer;
}

.navbar__list {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 40px;
  color: #ffffff;
}
@media (min-width: 768px) {
  .navbar__list {
    flex-direction: row;
    justify-content: space-between;
    width: 460px;
    color: #000000;
  }
}

.navbar-item:first-child {
  border-bottom: 2px solid #ffffff;
}

.navbar__link {
  padding: 20px;
  font-weight: 400;
  font-size: 18px;
  line-height: 24px;
  text-transform: uppercase;
  cursor: pointer;
  transition: 0.25s all;
}
.navbar__link:hover {
  opacity: 0.6;
}
@media (min-width: 768px) {
  .navbar__link {
    padding: 0 0 10px 0;
  }
}

@media (min-width: 1200px) {
  .navbar__link-index {
    color: #ffffff;
  }
}

.hamburger {
  border: none;
  border-bottom: 8px double black;
  border-top: 3px solid black;
  content: "";
  height: 14px;
  width: 24px;
  background-color: transparent;
}
@media (min-width: 768px) {
  .hamburger {
    display: none;
  }
}
.hamburger__list {
  display: none;
}

.layout {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  opacity: 0;
  visibility: hidden;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 99;
}
.show {
  opacity: 1;
  visibility: visible;
}

.active {
  border-bottom: 2px solid #68B738;
}

