input[type=range] {
  width: 84px;
  height: 14px;
  padding: 4px 6px;
  margin: 0 15px;
  border: 1px solid rgba(205, 205, 205, 0.8039215686);
  border-radius: 6px;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}
input[type=range]:focus {
  outline: none;
}
@media (min-width: 768px) {
  input[type=range] {
    width: 428px;
    height: 6px;
    background: #DCDCDC;
    border: none;
  }
}
input[type=range]::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 35px;
  height: 6px;
  background-color: #68B738;
  border-radius: 6px;
}
@media (min-width: 768px) {
  input[type=range]::-webkit-slider-thumb {
    width: 34px;
    height: 34px;
    background-color: transparent;
    background-image: url("../img/slider-control.svg");
    background-size: cover;
  }
}
input[type=range]::-moz-range-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 35px;
  height: 6px;
  background-color: #68B738;
  border-radius: 6px;
}
@media (min-width: 768px) {
  input[type=range]::-moz-range-thumb {
    width: 34px;
    height: 34px;
    background-color: transparent;
    background-image: url("../img/slider-control.svg");
    background-size: cover;
  }
}

