.form__radio-text {
  position: relative;
  padding-left: 45px;
  justify-content: flex-start;
}
.form__radio-text::before {
  position: absolute;
  top: -5px;
  left: 0;
  content: "";
  width: 30px;
  height: 30px;
  border: 1px solid #CDCDCD;
  border-radius: 100%;
}
.form__radio-text::after {
  position: absolute;
  top: 3px;
  left: 8.4px;
  transform: translate(-50%, -50%);
  content: "";
  width: 13.5px;
  height: 13.5px;
  border-radius: 100%;
  background-color: #68B738;
  opacity: 0;
  transform: scale(0.8);
}

.form__radio:checked + .form__radio-text::after {
  background-color: #68B738;
  transform: scale(1);
  opacity: 1;
  transition: 0.3s;
}

.form__checkbox {
  display: none;
}
.form__checkbox-text {
  position: relative;
  padding-left: 45px;
  justify-content: flex-start;
}
.form__checkbox-text::before {
  position: absolute;
  top: -5px;
  left: 0;
  content: "";
  width: 30px;
  height: 30px;
  border: 1px solid #CDCDCD;
}
.form__checkbox-text::after {
  position: absolute;
  top: 3px;
  left: 6.4px;
  transform: translate(-50%, -50%);
  content: "";
  width: 18px;
  height: 14px;
  background-repeat: no-repeat;
  background-position: 50%;
  opacity: 0;
  transform: scale(0.8);
}

.form__checkbox:checked + .form__checkbox-text::after {
  background-image: url(../img/icon-tick.svg);
  transform: scale(1);
  opacity: 1;
  transition: 0.3s;
}

